<script>

// Component
import feather from 'feather-icons';
import Loader from '@/interface/template/Loader.vue';
import Subhead from '@/interface/template/outline/Subhead.vue';
import storageManager from '@/plugins/storage';

export default {
    inject: ['apiServer'],
    data() {
      return {
        hostkey:this.$Key,
        accessToken: storageManager.get('session','token'),
        configs: storageManager.get('configs'),
        loading_sources: true,
        isEditMode: false, // Track whether in edit mode
        showModal: false, // To control the modal visibility
        spaceData: {
          name: '',
          status: '',
          spaceSize: '',
          s3Key: '',
          s3Endpoint: '',
          s3Hosting: '',
          s3Secret: '',
          s3Region: '',
          s3EndpointDefault: '',
          s3Bucket: '',
        },
        // Add a variable to store the item to edit
        listItemToEdit: null,
        listItem:[],
        showDeleteConfirmation: false,
        spaceToDelete: null,
      }
    },
    components: {
      Loader,
      Subhead
    },
    methods: {
      formatBytes(bytes) {
        var marker = 1024; // Change to 1000 if required
        var decimal = 3; // Change as required
        var kiloBytes = marker; // One Kilobyte is 1024 bytes
        var megaBytes = marker * marker; // One MB is 1024 KB
        var gigaBytes = marker * marker * marker; // One GB is 1024 MB
        if(bytes < kiloBytes) return bytes + " Bytes";
        else if(bytes < megaBytes) return(bytes / kiloBytes).toFixed(decimal) + " KB";
        else if(bytes < gigaBytes) return(bytes / megaBytes).toFixed(decimal) + " MB";
        else return(bytes / gigaBytes).toFixed(decimal) + " GB";
      },
      calculateDiskUsagePercentage(bytes,full) {
        const limitInBytes = full * 1024 * 1024; // 5000MB in bytes
        const percentage = (bytes / limitInBytes) * 100;
        return percentage.toFixed(2); // Display percentage with two decimal places
      },
      editItem(mode, item) {
        this.listItemToEdit = item; // Store the item to edit
        this.openModal(mode, item); // Open the modal with the mode and item's data
      },
      // Modify the openModal method to accept a mode parameter
      openModal(mode, initialFormData = {}) {
        this.isEditMode = mode === 'edit'; // Set edit mode based on the mode parameter
        this.spaceData = { ...initialFormData }; // Initialize form data
        this.showModal = true;
      },

      closeModal() {
        this.showModal = false;
      },
      submitForm() {
        if (this.isEditMode) {
          this.editData(this.listItemToEdit._id);
        } else {
          this.addData();
        }
      },
      async editData(id) {
        try {
          // Create a copy of this.spaceData without the _id field
          const dataWithoutId = { ...this.spaceData };
          delete dataWithoutId._id;

          const response = await fetch(`https://gateway.cloudrestfulapi.com/api/space/${id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json','client-token-key':this.hostkey
            },
            body: JSON.stringify({ data: dataWithoutId }), // Send dataWithoutId without _id
          });

          if (response.ok) {
            console.log('Item updated successfully');
            await this.getData();
            this.closeModal();
          } else {
            console.error('Failed to update item');
          }
        } catch (error) {
          console.error(error);
        }
      },
      async addData() {
        try {
          const response = await fetch('https://gateway.cloudrestfulapi.com/api/space', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json','client-token-key':this.hostkey
            },
            body: JSON.stringify({ data: this.spaceData }), // Remove the extra curly braces
          });

          if (response.ok) {
            console.log('Item added successfully');
            await this.getData();
            this.closeModal();
          } else {
            console.error('Failed to add item');
          }
        } catch (error) {
          console.error(error);
        }
      },
      async getData() {
        if (storageManager.get('session', 'login')) {
          try {
            this.loading_sources = false;

            let andConditions = [{}];
            const pipeline = [
              {
                $match: {
                  $and: andConditions,
                },
              },
            ];

            const requestOptions = {
              method: 'POST',
              headers: { 'Content-Type': 'application/json','client-token-key':this.hostkey },
              body: JSON.stringify({
                pipeline: pipeline,
              }),
            };

            const resAPI = await fetch('https://gateway.cloudrestfulapi.com/api/space/aggregate', requestOptions);
            const restReturn = await resAPI.json();

            const getAndUpdateSpaceData = async (space) => {
            const spaceId = space._id;
            const spaceData = await this.getSpaceData(spaceId);
            return {
              ...space,
              size: spaceData[0]?.totalSize || '0',   // Access totalSize property
              count: spaceData[0]?.itemCount || '0',  // Access itemCount property
            };
          };

            // Use Promise.all to fetch space data for all spaces in parallel
            const updatedListItems = await Promise.all(restReturn.map(getAndUpdateSpaceData));

            console.log(updatedListItems);

            this.listItem = updatedListItems;
            this.loading_sources = true;
          } catch (error) {
            console.log(error);
          }
        }
      },
      async getSpaceData(targetSpaceId) {
        if (storageManager.get('session', 'login')) {
          try {
            this.loading_sources = false;

            const pipeline = [
              {
                $match: {
                  spaceId: targetSpaceId, // Match the specific spaceId
                },
              },
              {
                $group: {
                  _id: null,
                  itemCount: { $sum: 1 }, // Count the matching items
                  totalSize: { $sum: "$size" }, // Sum the size of matching items
                },
              },
            ];

            const requestOptions = {
              method: 'POST',
              headers: { 'Content-Type': 'application/json','client-token-key':this.hostkey },
              body: JSON.stringify({
                pipeline: pipeline,
              }),
            };

            const resAPI = await fetch('https://gateway.cloudrestfulapi.com/api/storage/aggregate', requestOptions);
            const restReturn = await resAPI.json();

            return restReturn; // Return both itemCount and totalSize

          } catch (error) {
            console.log(error);
            return { itemCount: 0, totalSize: 0 }; // Return default values in case of an error
          }
        }
      },
      async deleteData(id) {
        if(storageManager.get('session','login')) {
          try {
            this.loading_sources = false;
            console.log("id",id)
            const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/space/" + id, {
              method: 'DELETE',
              headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
            });
            console.log(await resAPI);
            this.loading_sources = true;
            await this.getData();

          } catch (error) {
            console.log(error)
          }
        }
      },
      showDeleteDialog(space) {
        this.spaceToDelete = space;
        this.showDeleteConfirmation = true;
      },
      async confirmDelete() {
        try {
          await this.deleteData(this.spaceToDelete._id);
          this.showDeleteConfirmation = false;
        } catch (error) {
          console.error(error);
        }
      },
      cancelDelete() {
        this.showDeleteConfirmation = false;
      },
      updated() {
        feather.replace();
      },
    },
    async mounted () {
      try {
        await this.getData();
      } catch (error) {
        console.log(Error);
      }
    },
    setup() {
      //console.log("Setup");
    },
};
</script>

<template>

  <div v-if="!loading_sources" class="text-center"><Loader/></div>
  <Subhead  v-if="loading_sources"
    :navigation="
    [
      {
        name: 'ย้อนกลับ',
        link: '/',
        style: 'chevron-left',
        class: 'default-btn',
        visible: true,
        type: 'button',
      },
    ]"
  />
  <!-- Modal -->
  <div
    class="fixed top-0 left-0 w-full h-full flex items-center justify-center z-50"
    v-if="showModal"
  >
    <div class="fixed inset-0 bg-black opacity-50"></div>
    <div class="relative bg-white w-full md:w-1/2 lg:w-1/2 rounded shadow-lg">
      <!-- Modal Header -->
      <div class="bg-blue-500 text-white p-3 rounded-t">
        <h3 class="text-md font-semibold"><font-awesome-icon :icon="['fas','server']" class="text-white mr-1" /> {{ isEditMode ? 'แก้ไข Space' : 'เพิ่ม Space ใหม่' }}</h3>
        <button class="absolute top-2 right-3 text-2xl font-semibold hover:text-gray-200"
        @click="closeModal">&times;</button>
      </div>

      <!-- Modal Content -->
      <div class="p-3">
        <form>
          <div class="grid grid-cols-2 gap-2">

              <div class="mb-2">
                <label for="name" class="block text-gray-700 font-bold mb-2">Storage Name</label>
                <input
                  v-model="spaceData.name"
                  type="text"
                  id="name"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="space" class="block text-gray-700 font-bold mb-2">Disk Space</label>
                <input
                  v-model="spaceData.spaceSize"
                  type="text"
                  id="space"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3Key" class="block text-gray-700 font-bold mb-2">S3 Key</label>
                <input
                  v-model="spaceData.s3Key"
                  type="text"
                  id="s3Key"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3Endpoint" class="block text-gray-700 font-bold mb-2">S3 Endpoint</label>
                <input
                  v-model="spaceData.s3Endpoint"
                  type="text"
                  id="s3Endpoint"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3Hosting" class="block text-gray-700 font-bold mb-2">S3 Hosting</label>
                <input
                  v-model="spaceData.s3Hosting"
                  type="text"
                  id="s3Hosting"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3Secret" class="block text-gray-700 font-bold mb-2">S3 Secret</label>
                <input
                  v-model="spaceData.s3Secret"
                  type="text"
                  id="s3Secret"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3Region" class="block text-gray-700 font-bold mb-2">S3 Region</label>
                <input
                  v-model="spaceData.s3Region"
                  type="text"
                  id="s3Region"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3EndpointDefault" class="block text-gray-700 font-bold mb-2">S3 Default Endpoint</label>
                <input
                  v-model="spaceData.s3EndpointDefault"
                  type="text"
                  id="s3EndpointDefault"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <div class="mb-2">
                <label for="s3Bucket" class="block text-gray-700 font-bold mb-2">S3 Bucket</label>
                <input
                  v-model="spaceData.s3Bucket"
                  type="text"
                  id="s3Bucket"
                  class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
                />
              </div>

              <!-- Checkbox -->
              <div class="mb-2">
                <label class="block text-gray-700 font-bold mb-2">สถานะ</label>
                <input
                  type="checkbox"
                  v-model="spaceData.status"
                  class="form-checkbox h-5 w-5 text-blue-600"
                /> ใช้งาน
              </div>
      
          </div>
        </form>
      </div>

      <!-- Modal Footer -->
      <div class="bg-gray-100 px-4 py-3 flex justify-end rounded-b">
        <button
          type="button"
          class="px-4 py-2 text-white bg-blue-500 rounded hover:bg-blue-600 mr-2"
          @click="closeModal"
        >
          ปิด
        </button>
        <button
          type="button"
          @click="submitForm"
          class="px-4 py-2 text-white bg-green-500 rounded hover:bg-green-600"
        >
          <font-awesome-icon :icon="['fas','save']" class="text-white mr-1" />
          {{ isEditMode ? 'อัพเดตข้อมูล' : 'เพิ่มข้อมูล' }}
        </button>
      </div>
    </div>
  </div>

  <div class="flex-1 pb-8 bg-gray-100 pt-2 pb-5 border-t" v-if="loading_sources">
    <div class="mt-8">
      <div class="mx-auto max-w-7xl px-6 sm:px-6 lg:px-6">

        <div class="mt-8 flex flex-col">
          <div class="-my-2 -mx-4 overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div class="inline-block min-w-full py-2 align-middle md:px-6 lg:px-8">

              <div class="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
             
                <div v-for="(space) in listItem" :key="space._id" class="bg-white rounded-lg shadow-md overflow-hidden">
                  <div class="w-full h-30 relative text-center object-cover pt-10">

                    <div class="w-7 h-7 absolute top-2 right-2 text-white pt-[6px] text-sm font-normal rounded-full text-center" :class="space.status ? 'bg-green-600' : 'bg-red-600'">
                      <font-awesome-icon :icon="space.status ? ['fas', 'check-circle'] : ['fas', 'times']" class="text-white" />
                    </div>
                    
                    <div class="flex flex-col items-center justify-center">
                      <div class="relative text-center w-1/2">
                        <div class="absolute w-full text-xs pt-2 text-white">{{this.calculateDiskUsagePercentage((space.size/1024),space.spaceSize)}}%</div>
                        <font-awesome-icon :icon="['fas','hdd']" class="text-blue-600 text-[60px] mb-4" />
                        <div class="absolute w-full bg-gray-200 h-[6px] bottom-0">

                          <div class="absolute bottom-0" 
                            :class="{
                              'bg-green-500': this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize) < 50,
                              'bg-blue-500': this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize) >= 50 && this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize) < 70,
                              'bg-orange-500': this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize) >= 70 && this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize) < 90,
                              'bg-red-500': this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize) >= 90
                            }"
                            :style="'width:' + Math.min(this.calculateDiskUsagePercentage((space.size / 1024), space.spaceSize), 100) + '%;'"
                            style="height: 6px;">
                        </div>

                        </div>
                      </div>
                    </div>
                  </div>               
                  <div class="p-4">
                    
                    <p class="font-bold text-gray-600">{{ space.name }}</p>
                    <p class="text-xs text-gray-400">{{this.formatBytes(space.size)}} / {{ space.spaceSize }} GB</p>
                    <p class="text-xs text-gray-400">{{space.count}} Files</p>
                  </div>
                  <div class="bg-gray-100 border-t-2 border-gray-200 py-2 px-4 flex justify-between items-center">
                    <a href="javascript:void(0);" @click="editItem('edit',space)" class="text-gray-900 hover:text-gray-500 text-md font-medium">
                      <font-awesome-icon :icon="['fas','square-pen']" class="text-black mt-[3px] mr-2 text-md"/>
                      <span>แก้ไข</span>
                    </a>
                    <a href="javascript:void(0);" @click="showDeleteDialog(space)" class="text-gray-900 hover:text-gray-500 text-md font-medium">
                      <font-awesome-icon :icon="['fas','trash-can']" class="text-black mt-[3px] mr-2 text-md"/>
                      <span>ลบ</span>
                    </a>
                  </div>
                </div>

                <!-- Button to open the modal for adding -->
                <button class="bg-white p-10 rounded-lg shadow-md overflow-hidden flex flex-col items-center justify-center cursor-pointer p-10"
                @click="openModal('add')"
                >
                  <div class="w-12 h-12 rounded-full bg-blue-500 text-white flex items-center justify-center hover:opacity-70">
                    <font-awesome-icon :icon="['fas','plus']" class="text-3xl"/>
                  </div>
                  <p class="text-gray-500 mt-2 text-center">Add Space</p>
                </button>
                
              </div>
              
            </div>
          </div>
        </div>


    </div>
  </div>
</div>

<!-- Delete confirmation dialog (inline) -->
<div v-if="showDeleteConfirmation" class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
  <!-- Modal -->
  <div class="bg-white rounded shadow-lg w-96">
    <!-- Modal Header -->
    <div class="bg-red-500 text-white p-2 rounded-t">
      <h3 class="text-base"><font-awesome-icon :icon="['fas','trash']" class="text-white"/> ยืนยันการลบข้อมูล</h3>
    </div>
    <!-- Modal Content -->
    <div class="p-4">
      <p class="text-red-500 text-center">
        คุณต้องการลบข้อมูล <strong>"{{ spaceToDelete.name }}"</strong> ? <br> หลังจากลบแล้ว ไม่สามารถกู้คืนได้
      </p>
    </div>
    <!-- Modal Footer -->
    <div class="bg-gray-100 py-2 px-4 flex justify-end rounded-b">
      <button
        @click="cancelDelete"
        class="px-2 py-1 text-gray-500 hover:text-gray-700 mr-2"
      >
        ยกเลิก
      </button>
      <button
        @click="confirmDelete"
        class="px-2 py-1 text-red-500 hover:text-red-700 font-medium"
      >
        ลบเลย
      </button>
    </div>
  </div>
</div>

</template>